}

.logo {
    width: 7rem;
}

nav {
    z-index: 2;
}

nav a.nav-link {
    font-weight: bolder;
    letter-spacing: 0.3rem;
    cursor: pointer;
    border-bottom: 0.1875rem solid var(--primary-red);
}

nav ul li {
    text-transform: uppercase;
}

.navbar-nav>li>a {
    color: var(--primary-white)!important;
}

.navbar-toggler-icon {
    border: none;
}

.navbar-nav>li>a.active, .navbar-nav>li>a.active:focus {
    border-bottom: 0.1875rem solid var(--primary-orange);
}

.navbar-nav>li>a:hover {
    border-bottom: 0.1875rem solid var(--primary-orange);
}

/*************************** header ****************************/

.header-wrapper {
    position: relative;
    background: url(g3.jpeg) no-repeat;
    background-size: cover;
    background-position: center;
    height: 90vh;
}

.main-info {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 1;
}

.main-info h1 {
    font-size: 3rem;
    background: -webkit-linear-gradient(rgb(230, 33, 66), rgb(209, 72, 202));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: uppercase;
    font-weight: bold;
}

@media(max-width:768px) {
    .main-info h1 {
        font-size: 1.7rem!important;
    }
    .typed-text {
        font-size: 1.3rem!important;
    }
}

/* typed text**********/

.typed-text {
    font-size: 2rem;
    color: var(--Lighter-Indigo);
}

/* Header main offer button**********/

.btn-main-offer {
    border: 1px solid red;
    text-transform: uppercase;
    border-radius: 0;
    padding: 0.75rem;
    color: var(--primary-red);
    margin: 2rem;
}

.btn-main-offer:hover {
    text-decoration: none;
    background-color: var(--primary-red);
    color: var(--primary-white);
    transition: .3s ease-in-out;
}

.btn-main-offer a {
    text-decoration: none!important;
}

/* particles **********/

.tsparticles-canvas-el {
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}

